.main {
	width: 231px;
	/*margin: 0 auto;*/
}

.input {
	border: 2px solid #111111;
	border-radius: 5px;
	padding: 2px 4px;
}

.addBtn {
	display: inline-block;
	width: 40px;
	height: 20px;
	margin: 5px;
	border: none;
	outline: none;
	color: #fff;
	background: #111;
	cursor: pointer;
	position: relative;
	z-index: 0;
	border-radius: 5px;
}

.addBtn:before {
	content: '';
	background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
	position: absolute;
	top: -2px;
	left:-2px;
	background-size: 400%;
	z-index: -1;
	filter: blur(5px);
	width: calc(100% + 4px);
	height: calc(100% + 4px);
	animation: glowing 20s linear infinite;
	opacity: 0;
	transition: opacity .3s ease-in-out;
	border-radius: 5px;
}

.addBtn:active {
	color: #000
}

.addBtn:active:after {
	background: transparent;
}

.addBtn:hover:before {
	opacity: 1;
}

.addBtn:after {
	z-index: -1;
	content: '';
	position: absolute;
	width: 100%;
	height: 100%;
	background: #111;
	left: 0;
	top: 0;
	border-radius: 5px;
}

@keyframes glowing {
	0% { background-position: 0 0; }
	50% { background-position: 400% 0; }
	100% { background-position: 0 0; }
}

.error {
	border: 2px solid red;
	outline: transparent;
}

.errorWrapper {
	width: 150px;
	height: 20px;
	padding-left: 30px;
}

.errorMessage {
	margin-left: 10px;
	color: firebrick;
}